import React, {useEffect, useState} from 'react';
import {compose} from "redux";
import withRouter from "../common/withRouter/withRouter";

const ProfileStatusFunction = (props) => {
    const [editMode, setEditMode] = useState(false);
    const [status, setStatus] = useState(props.status);

    useEffect(() => {
        setStatus(props.status)
    }, [props.status]);

    const activateEditMode = () => {
        setEditMode(true)
    }
    const updateStatusInput = (e) => {
        setStatus(e.target.value);
    }
    const deactivateEditMode = () => {
        setEditMode(false)
        props.updateStatus(status);
    }

    return (
        <div>
            {props.match.params.userId
                ? <>Status: <span data-testid={'status'}>{props.status ? props.status : "no status"}</span></>
                : <>
                    {
                        editMode
                            ? <>
                                Status:
                                <input onChange={updateStatusInput} type="text" value={status}/>
                                <button onClick={deactivateEditMode}>Save</button>
                            </>
                            : <>
                                <>
                                    Status:
                                    <span data-testid={'status'}>{props.status === '' ? 'no status' : props.status}</span>
                                    <button data-testid={'btnEditMode'} onClick={activateEditMode}>Edit</button>
                                </>
                            </>
                    }
                </>}
        </div>
    );
};

export default compose(withRouter)(ProfileStatusFunction);