import {authThunkCreator} from "./auth-reducer";

const SET_INITIALIZED_SUCCESS = 'socialNetwork/app/SET_INITIALIZED_SUCCESS';

export const setInitializedSuccess = () => ({type: SET_INITIALIZED_SUCCESS});

let initialState = {
    initialized: false
}

const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_INITIALIZED_SUCCESS: {
            return {
                ...state,
                initialized: true
            }
        }
        default :
            return state;
    }
}

// very useful and important thing!!!
            export const initializeApp = () => (dispatch) => {
                let promise = dispatch(authThunkCreator());
                Promise.all([promise]).then(() => {
                    dispatch(setInitializedSuccess());
                })
            }
// very useful and important thing!!!


export default appReducer;