import header_mod from '../.././css_modules/Header.module.css';
import Preloader from '../common/preloader/Preloader';
import {NavLink} from "react-router-dom";

const Header = (props) => {
    return (
        <header className={header_mod.header}>
            {props.isFetching
                ? <Preloader />
                : <>
                    <img src='https://cdn-icons-png.flaticon.com/128/10963/10963275.png' alt='icon' />
                    <div className={header_mod.loginInfo}>
                        <p>{props.isAuth ? <div>Hello, {props.login} - <button onClick={props.logOut}>Log out</button></div> : <NavLink to={'/login'}><button>Log in</button></NavLink>}</p>
                    </div>
                </>}


        </header>
    );
}

export default Header;