import React, {useState} from 'react';
import users_styles from "../../css_modules/Users.module.css";
import users from "./Users";

const Paginator = (props) => {
    let pagesCount = Math.ceil(props.totalCount / props.pageSize);
    console.log(pagesCount)
    let pages = [];
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i);
    }

    const portionCount = Math.ceil(pagesCount / props.portionSize);
    const [currentPortionNumber, setCurrentPortionNumber] = useState(Math.ceil(props.currentPage / props.portionSize));
    const leftPortionNumber = (currentPortionNumber - 1) * props.portionSize + 1;
    const rightPortionNumber = currentPortionNumber * props.portionSize;
    return (
        <div className={users_styles.pagesContainer}>
            {currentPortionNumber > 1
                ? <button onClick={() => setCurrentPortionNumber(currentPortionNumber - 1)}>PREV</button>
                : ""
            }
            {
                pages
                    .filter(p => p >= leftPortionNumber && p <= rightPortionNumber)
                    .map(p => {
                    return <button onClick={() => {
                        props.pageCountHandler(p)
                    }} key={p} className={props.currentPage === p ? users_styles.activePageBtn + " " + users_styles.pageBtn : users_styles.pageBtn}>{p}</button>
                })
            }
            {currentPortionNumber < portionCount
                ? <button onClick={() => setCurrentPortionNumber(currentPortionNumber + 1)}>NEXT</button>
                : ""
            }
        </div>
    );
};

export default Paginator;