const ADD_MESSAGE = 'ADD-MESSAGE';
export const addMessage = (text) => ({ type: ADD_MESSAGE, newText: text });

let initialState = {
    dialogs: [
        { id: 2, name: "Papa", icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
        { id: 1, name: "Mama", icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
        { id: 3, name: "Druzhban", icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
        { id: 4, name: "Druzhban", icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
        { id: 5, name: "Druzhban", icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
        { id: 6, name: "Druzhban", icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
    ],
    messages: [
        { id: 1, message: "Yo!", ownerIsMe: true, icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
        { id: 2, message: "Hello world!", ownerIsMe: false, icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
        { id: 3, message: "Druzhban, hello!", ownerIsMe: false, icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
        { id: 4, message: "Druzhban, hello!", ownerIsMe: true, icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
        { id: 5, message: "Druzhban, hello!", ownerIsMe: true, icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" },
    ],
};

const dialogsReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_MESSAGE: {
            return { 
                ...state,
                messages: [...state.messages, { id: 6, 
                                                message: action.newText,
                                                ownerIsMe: true, 
                                                icon: "https://louisville.edu/enrollmentmanagement/images/person-icon/image" }],
            };
        }
        default:
            return state;
    }
}

export default dialogsReducer;