import axios from "axios";

const instance = axios.create({
    withCredentials: true,
    headers: {
        "API-KEY": '965c11b7-e2b1-455b-8668-299c1c75512a'
    },
    baseURL: 'https://social-network.samuraijs.com/api/1.0/'
})

export const callApi = {
    getAuthMe() {
        return instance.get('auth/me').then(response => response.data)
    },
    getUserProfile(userId) {
        return instance.get('profile/' + userId).then(response => response.data)
    },
    getUsers(currentPage, pageSize) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`).then(response => response.data)
    },
    followUser(u) {
        return instance.post(`follow/${u.id}`, {}).then(response => response.data)
    },
    unfollowUser(u) {
        return instance.delete(`follow/${u.id}`).then(response => response.data)
    },
    getStatus(userId) {
        return instance.get(`profile/status/${userId}`).then(response => response.data)
    },
    updateStatus(status) {
        return instance.put(`profile/status`, {status: status})
    },
    login(email, password, rememberMe) {
        return instance.post(`auth/login`, {email, password, rememberMe}).then(response => response.data)
    },
    logout() {
        return instance.delete(`auth/login`).then(response => response.data)
    },
    saveAvatar(image) {
        const formData = new FormData();
        formData.append('image', image);
        return instance.put(`profile/photo`, formData).then(response => response.data)
    }
}