import {HashRouter, Route, Routes} from 'react-router-dom';
import './App.css';
import Navigation from './components/Navigation/Navigation';
import DialogsContainer from './components/Dialogs/DialogsContainer';
import UsersContainer from './components/Users/UsersContainer';
import ProfileContainer from './components/Main/ProfileContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import LoginContainer from "./components/Login/LoginContainer";
import React, {Component} from "react";
import {compose} from "redux";
import {connect, Provider} from "react-redux";
import Preloader from "./components/common/preloader/Preloader";
import {initializeApp} from "./redux/app-reducer";
import store from "./redux/redux-store";
import withSuspense from "./components/common/withSuspense/withSuspense";
const News = withSuspense(React.lazy(() => import('./components/News/News')))
const Music = withSuspense(React.lazy(() => import('./components/Music/Music')))
const Settings = withSuspense(React.lazy(() => import('./components/Settings/Settings')))

class App extends Component {
    componentDidMount() {
        this.props.initializeApp();
    }

    render() {
        if(!this.props.initialized) {
            return <Preloader />
        }

        return (
            <div className='App'>
                <div className='app-wrapper'>
                    <HeaderContainer/>
                    <Navigation state={this.props.state}/>
                    <div className='content-wrapper-page'>
                        <Routes>
                            <Route path='/profile/:userId?' element={<ProfileContainer/>}/>
                            <Route path='/dialogs/*' element={<DialogsContainer/>}/>
                            <Route path='/news/*' element={<News/>}/>
                            <Route path='/music/*' element={<Music/>}/>
                            <Route path='/settings/*' element={<Settings/>}/>
                            <Route path='/users/*' element={<UsersContainer/>}/>
                            <Route path='/login/*' element={<LoginContainer/>}/>
                        </Routes>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized,
    state: state
})

const AppContainer = compose(connect(mapStateToProps, {initializeApp}))(App);
const MainApp = () => {
    return <HashRouter>
        <Provider store={store}>
            <AppContainer/>
        </Provider>
    </HashRouter>
}

export default MainApp






