import {callApi} from "../api/api";
import {stopSubmit} from "redux-form";

const SET_AUTH_DATA = 'socialNetwork/auth/SET_AUTH_DATA';
const SET_AUTH = 'socialNetwork/auth/SET_AUTH';
const SET_FETCH = 'socialNetwork/auth/SET_FETCH';
const SET_SUCCESS = 'socialNetwork/auth/SET_SUCCESS';
const SET_LOGIN = 'socialNetwork/auth/SET_LOGIN';

export const setAuthData = (id, email, login) => ({type: SET_AUTH_DATA, data: {id, email, login}});
export const setLoginData = (id, email, password, rememberMe) => ({
    type: SET_LOGIN,
    data: {id, email, password, rememberMe}
});
export const setIsAuth = (isAuth) => ({type: SET_AUTH, isAuth});
export const setIsFetching = (isFetch) => ({type: SET_FETCH, isFetch});
export const setSuccess = () => ({type: SET_SUCCESS});

let initialState = {
    id: null,
    email: null,
    login: null,
    isFetching: false,
    isAuth: false,
    rememberMe: false,
    success: false
}

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_AUTH: {
            return {
                ...state,
                isAuth: action.isAuth
            }
        }
        case SET_AUTH_DATA: {
            return {
                ...state,
                ...action.data
            }
        }
        case SET_FETCH: {
            return {
                ...state,
                isFetching: action.isFetch
            }
        }
        case SET_LOGIN: {
            return {
                ...state,
                ...action.data
            }
        }
        case SET_SUCCESS: {
            return {
                ...state,
                success: true
            }
        }
        default:
            return state;
    }
}

export const authThunkCreator = () => async (dispatch) => {
    dispatch(setIsFetching(true));
    let data = await callApi.getAuthMe();
        if (data.resultCode === 0) {
            let {id, email, login} = data.data;
            dispatch(setAuthData(id, email, login));
            dispatch(setIsAuth(true));
            dispatch(setSuccess());
        } else {
            dispatch(setIsAuth(false))
        }
        dispatch(setIsFetching(false))
}

export const loginThunkCreator = (email, password, rememberMe) => async (dispatch) => {
    dispatch(setIsFetching(true));
    let response = await callApi.login(email, password, rememberMe);
        if (response.resultCode === 0) {
            dispatch(setLoginData(response.id, email, password, rememberMe));
            dispatch(authThunkCreator());
            dispatch(setIsAuth(true));
            dispatch(setIsFetching(false));
        } else {
            dispatch(setIsAuth(false));
            dispatch(stopSubmit('login', {_error: response.messages.toString()}));
            dispatch(setIsFetching(false));
        }
}

export const logoutThunkCreator = () => async (dispatch) => {
    dispatch(setIsFetching(true));
    let response = callApi.logout();
        if (response.resultCode === 0) {
            dispatch(setAuthData(null, null, null));
            dispatch(setLoginData(null, null, null, false));
            dispatch(setIsAuth(false));
            dispatch(setIsFetching(false));
        }
}

export default authReducer;