import React from 'react';
import {Field, reduxForm} from "redux-form";
import login_mod from '../../css_modules/Login.module.css';
import {CustomInput} from "../common/fieldsCustomComponents/SetCustomArea";
import {maxLength, required} from "../../utils/formValidateControls/formValidateControls";
import {Navigate} from "react-router-dom";

const maxLength20 = maxLength(20);

const LoginForm = (props) => {
    return (
        <div>
            <form onSubmit={props.handleSubmit}>
                <div><Field name={'email'} component={CustomInput} type="text" placeholder={'Email'} validate={[required]}/></div>
                <div><Field name={'password'} component={CustomInput} type="password" placeholder={'Password'} validate={[required, maxLength20]}/></div>
                <div><label><Field name={'rememberMe'} component={CustomInput} type="checkbox"/><p> remember me</p></label></div>
                <div><button>Log in</button></div>
            </form>
            {props.error && <div className={login_mod.errorMessageWrapper}>{props.error}</div>}
        </div>
    );
};

const LoginReduxForm = reduxForm({
    form: 'login'
})(LoginForm);

const Login = (props) => {
    const onSubmit = (formdata) => {
        props.login(formdata.email, formdata.password, formdata.rememberMe);
    }
    return (
        <div>
            <h1>Login</h1>
            <LoginReduxForm onSubmit={onSubmit} />
            {props.isAuth === true && <Navigate to="/profile" />}
        </div>
    );
};

export default Login;