import {connect} from 'react-redux';
import {getProfileThunkCreator, savePhotoThunkCreator, updateStatusThunkCreator} from '../../redux/profile-reducer';
import Profile from './Profile';
import React from 'react';
import Redirect from "../common/redirect/Redirect";
import {compose} from "redux";
import Preloader from "../common/preloader/Preloader";
import withRouter from "../common/withRouter/withRouter";
import {getAuthSuccess, getIsAuth, getState, getUserId} from "../../redux/selectors";

class ProfileContainerClass extends React.Component {
    componentDidMount() {
        if(this.props.authSuccess) {
            this.props.getProfile(this.props.match.params.userId, this.props.myUserId)
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(this.props.authSuccess !== prevProps.authSuccess) {
            this.props.getProfile(this.props.match.params.userId, this.props.myUserId)
        }
        if(this.props.match.params.userId !== prevProps.match.params.userId) {
            this.props.getProfile(this.props.match.params.userId, this.props.myUserId)
        }
    }

    render() {
        return <>{this.props.authSuccess ? <Profile {...this.props} isOwner={!this.props.match.params.userId} /> : <Preloader />}</>
    }
}

const mapStateToProps = (state) => {
    return {
        state: getState(state).ProfilePage,
        myUserId: getUserId(state),
        isAuth: getIsAuth(state),
        authSuccess: getAuthSuccess(state),
        profile: state.ProfilePage.profile
    }
}

export default compose(connect(mapStateToProps, {getProfile: getProfileThunkCreator, updateStatus: updateStatusThunkCreator, savePhoto: savePhotoThunkCreator}), withRouter, Redirect)(ProfileContainerClass)

