import Post from "./Post";
import post_input_mod from '../../css_modules/Post_input.module.css';
import {Field, reduxForm} from "redux-form";
import {maxLength, required} from "../../utils/formValidateControls/formValidateControls";
import {CustomTxtarea} from "../common/fieldsCustomComponents/SetCustomArea";
import {Component, PureComponent} from "react";
import React from "react";

const maxLength20 = maxLength(20);

const PostForm = (props) => {
    return (
        <form className={post_input_mod.content_mypost} onSubmit={props.handleSubmit}>
            <Field name={'message'} component={CustomTxtarea} type={'text'} placeholder={'Your post message...'} validate={[required, maxLength20]}/>
            <button className={post_input_mod.submit_input}>Send</button>
        </form>
    )
}

const PostReduxForm = reduxForm({form: 'postform'})(PostForm)

const Posts = React.memo(props => {
    const onSubmit = (formdata) => {
        props.addPost(formdata.message)
        console.log(formdata)
    }
    console.log('render')
    return (
        <div>
            <h3>My posts</h3>
            <PostReduxForm onSubmit={onSubmit}/>
            <div className="content-post-titles">
                {
                    props.posts.map((post) => (
                        <div key={post.id}>
                            <Post message={post.message} likes={post.likes} dislikes={post.dislikes} deletePost={props.deletePost} id={post.id}/>
                        </div>
                    ))
                }
            </div>
        </div>
    )
})

export default Posts;