import dialogs_mod from '../.././css_modules/Dialogs.module.css';

const Message = (props) => {
    return (
        <div className={props.ownerIsMe === true ? dialogs_mod.mymsg : dialogs_mod.notmymsg}>
            <div className={dialogs_mod.messcon}>
                <img className={dialogs_mod.icon} src={props.icon} alt="icon" />
                <li>{props.message}</li>
            </div>

        </div>

    )
}

export default Message;