import {callApi} from "../api/api";

const ADD_POST = 'socialNetwork/profile/ADD-POST';
const UPDATE_PROFILE = 'socialNetwork/profile/UPDATE_PROFILE';
const UPDATE_STATUS = 'socialNetwork/profile/UPDATE_STATUS';
const DELETE_POST = 'socialNetwork/profile/DELETE_POST';
const SAVE_PHOTO = 'socialNetwork/profile/SAVE_PHOTO';

export const addPost = (text) => ({ type: ADD_POST, newText: text });
export const updateProfile = (profile) => ({ type: UPDATE_PROFILE, profile });
export const updateStatus = (status) => ({ type: UPDATE_STATUS, status });
export const deletePost = (id) => ({ type: DELETE_POST, id });
export const savePhotoSuccess = (photos) => ({type: SAVE_PHOTO, photos})

let initialState = {
    posts: [
        { id: 1, message: "Hello, world!", likes: 15, dislikes: 10 },
        { id: 2, message: "Hello, guys!", likes: 40, dislikes: 0 },
        { id: 3, message: "This is my first post!", likes: 54, dislikes: 3 },
        { id: 4, message: "Hello, world", likes: 17, dislikes: 9 },
    ],
    profile: null,
    status: ''
};

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_POST: {
                return {
                    ...state,
                    posts: [...state.posts, { id: 5, message: action.newText, likes: 0, dislikes: 0 }],
                    txtValue: ''
                };
        }
        case UPDATE_PROFILE: {
            return { ...state, profile: action.profile }
        }
        case UPDATE_STATUS: {
            return { ...state, status: action.status }
        }
        case DELETE_POST: {
            return {...state, posts: state.posts.filter(post => post.id !== action.id) };
        }
        case SAVE_PHOTO: {
            return {...state, profile: {...state.profile, photos: action.photos} }
        }
        default:
            return state;
    }
}

export const getProfileThunkCreator = (paramsId, myUserId) => async (dispatch) => {
    if (!paramsId) {
            if (myUserId) {
                let myDataProfile = await callApi.getUserProfile(myUserId);
                let myDataStatus = await callApi.getStatus(myUserId);
                dispatch(updateProfile(myDataProfile));
                dispatch(updateStatus(myDataStatus));
            }
    } else {
        let paramsDataProfile = await callApi.getUserProfile(paramsId);
        let paramsDataStatus = await callApi.getStatus(paramsId);
        dispatch(updateProfile(paramsDataProfile));
        dispatch(updateStatus(paramsDataStatus))
    }
}

export const updateStatusThunkCreator = (status) => async (dispatch) => {
    let response = await callApi.updateStatus(status)
        if(response.data.resultCode === 0){
            dispatch(updateStatus(status))
        }else{
            alert('something went wrong... try again!')
        }
}

export const savePhotoThunkCreator = (photo) => async (dispatch) => {
    let response = await callApi.saveAvatar(photo);
    if(response.resultCode === 0) {
        dispatch(savePhotoSuccess(response.data.photos));
    }
}

export default profileReducer;