import React from 'react';
import dialogs_mod from '../.././css_modules/Dialogs.module.css';
import DialogItem from './DialogItem';
import Message from './Message';
import {reduxForm, Field} from "redux-form";
import {maxLength, required} from "../../utils/formValidateControls/formValidateControls";
import {CustomTxtarea} from "../common/fieldsCustomComponents/SetCustomArea"
import post_input_mod from "../../css_modules/Post_input.module.css";

const maxLength50 = maxLength(20);

const DialogForm = (props) => {
    return (
        <form className={dialogs_mod.form} onSubmit={props.handleSubmit}>
            <Field name={'dialogMsg'} component={CustomTxtarea} placeholder={'Your message...'} validate={[required, maxLength50]} />
            <button className={dialogs_mod.form_btn}>Send</button>
        </form>
    )
}

const DialogReduxForm = reduxForm({form: 'dialogForm'})(DialogForm);

const Dialogs = (props) => {
    const onSubmit = (formData) => {
        props.addMessage(formData.dialogMsg);
        console.log(formData);
    }

    return (
        <main className={dialogs_mod.dialogs_main}>
        <div className={dialogs_mod.dialogs}>
                <div className={dialogs_mod.dialogs_ul}>
                    {
                        props.state.DialogsPage.dialogs.map((dialog) => (
                            <div key={dialog.id}>
                                <DialogItem name={dialog.name} id={dialog.id} />
                            </div>
                        ))
                    }
                </div>
            </div>
            <div className={dialogs_mod.messages}>
                <ul className={dialogs_mod.messages_ul}>
                    {
                        props.state.DialogsPage.messages.map((message) => (
                            <div key={message.id}>
                                <Message message={message.message} ownerIsMe={message.ownerIsMe} icon={message.icon} />
                            </div>
                        ))
                    }
                </ul>
            </div>
            <div className={dialogs_mod.formwrap}>
                <DialogReduxForm onSubmit={onSubmit}/>
            </div>
        </main>
    )
}

export default Dialogs;