import { NavLink } from 'react-router-dom';
import nav_mod from '../.././css_modules/Navigation.module.css';


const Navigation = (props) => {
    return (
        <div className={nav_mod.nav_list}>
            <nav>
                <NavLink to="/profile" className={navData => navData.isActive ? nav_mod.active : nav_mod.div}>Profile</NavLink>
                <NavLink to="/dialogs" className={navData => navData.isActive ? nav_mod.active : nav_mod.div}>Messages</NavLink>
                <NavLink to="/news" className={navData => navData.isActive ? nav_mod.active : nav_mod.div}>News</NavLink>
                <NavLink to="/music" className={navData => navData.isActive ? nav_mod.active : nav_mod.div}>Music</NavLink>
                <NavLink to="/users" className={navData => navData.isActive ? nav_mod.active : nav_mod.div}>Users</NavLink>
                <NavLink to="/settings" className={navData => navData.isActive ? nav_mod.active : nav_mod.div}>Settings</NavLink>
            </nav>
            <div className={nav_mod.friends}>
                <h3>Friends</h3>
                <div className={nav_mod.friendContainer}>
                    {
                        props.state.Sidebar.friends.map((friend, key) => (
                            <div className={nav_mod.friend} key={key}>
                                <p>{friend.name}</p>
                                <img className={nav_mod.friendIcon} src={friend.icon} alt="icon" />
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>
    );
}

export default Navigation;