export const maxLength = (maxLength) => (value) => {
    if (value && value.length > maxLength) {
        return `Maximal length: ${maxLength} symbols`;
    } else {
        return undefined;
    }
}
export const required = (value) => {
    if (value && value !== "") {
        return undefined
    } else {
        return 'Field is required';
    }
}