import user_mod from '../.././css_modules/User.module.css';
import Preloader from '../common/preloader/Preloader';
import ProfileStatus from "./ProfileStatusFunction";
import userImg from '../../assets/images/user.png'
import React from "react";

 const User = (props) => {

    const savePhotoOnChange = (e) => {
        if(e.target.files.length) {
            props.savePhoto(e.target.files[0]);
        }
    }
    if (!props.profileInfo) {
        return <Preloader />
    } else {
        return (
            <div className={user_mod.content_user}>
                <div className={user_mod.user_img_container}>
                    <img className={user_mod.user_img} src={!props.profileInfo.photos.small ? userImg : props.profileInfo.photos.small} alt="ava" />
                </div>
                {props.isOwner ? <input type="file" onChange={savePhotoOnChange}/> : ''}
                <div className="user_info">
                    <h3>Profile Name: {props.profileInfo.fullName}</h3>
                    <ProfileStatus status={props.status} updateStatus={props.updateStatus} />
                    <p>About Me: {props.profileInfo.aboutMe}</p>
                    <h3>Job: {props.profileInfo.lookingForAJob ? "I'm looking for a job right now" : "I'm not looking for a job right now"}</h3>
                    <p>Job description: {props.profileInfo.lookingForAJobDescription}</p>
                    <h3>Contacts:</h3>
                    <p>Facebook: {props.profileInfo.contacts.facebook !== null ? <a href={props.profileInfo.contacts.facebook} target="_blank">{props.profileInfo.contacts.facebook}</a> : "no Facebook"}</p>
                    <p>Website: {props.profileInfo.contacts.website !== null ? <a href={props.profileInfo.contacts.website} target="_blank">{props.profileInfo.contacts.website}</a> : "no website"}</p>
                    <p>VK: {props.profileInfo.contacts.vk !== null ? <a href={props.profileInfo.contacts.vk} target="_blank">{props.profileInfo.contacts.vk}</a> : "no VK"}</p>
                    <p>Twitter: {props.profileInfo.contacts.twitter !== null ? <a href={props.profileInfo.contacts.twitter} target="_blank">{props.profileInfo.contacts.twitter}</a> : "no Twitter"}</p>
                    <p>Instagram: {props.profileInfo.contacts.instagram !== null ? <a href={props.profileInfo.contacts.instagram} target="_blank">{props.profileInfo.contacts.instagram}</a> : "no Instagram"}</p>
                    <p>Youtube: {props.profileInfo.contacts.youtube !== null ? <a href={props.profileInfo.contacts.youtube} target="_blank">{props.profileInfo.contacts.youtube}</a> : "no Youtube"}</p>
                    <p>Github: {props.profileInfo.contacts.github !== null ? <a href={props.profileInfo.contacts.github} target="_blank">{props.profileInfo.contacts.github}</a> : "no Github"}</p>
                    <p>Main Link: {props.profileInfo.contacts.mainLink !== null ? <a href={props.profileInfo.contacts.mainLink} target="_blank">{props.profileInfo.contacts.mainLink}</a> : "no main link"}</p>
                </div>
            </div>
        );
    }
 }

 export default User;
