import User from "./User";
import main_mod from '../.././css_modules/Main.module.css';
import React from 'react';
import PostsContainer from "./PostsContainer";


const Profile = React.memo(props => {
    return (
        <main>
            {props.isAuth
                && <><img className={main_mod.main_img} src="https://helpx.adobe.com/content/dam/help/en/photoshop/using/convert-color-image-black-white/jcr_content/main-pars/before_and_after/image-before/Landscape-Color.jpg" alt="" />
                    <div>
                        <User savePhoto={props.savePhoto} status={props.state.status} updateStatus={props.updateStatus} profileInfo={props.profile} isOwner={props.isOwner}/>
                        <PostsContainer />
                    </div> </>
            }

        </main>
    );
})

export default Profile;