import {NavLink} from 'react-router-dom';
import userPhoto from '../../assets/images/user.png';
import users_styles from '../../css_modules/Users.module.css';
import Paginator from "./Paginator";

const Users = (props) => {
    debugger
    return (
        <>
            <h1>Users</h1>
            <Paginator totalCount={props.totalCount} pageSize={props.pageSize} currentPage={props.currentPage} pageCountHandler={props.pageCountHandler} portionSize={10}/>
            <div className={users_styles.wrapper}>
                {
                    props.users.map(u => (
                        <div key={u.id} className={users_styles.userContainer}>
                            <div className={users_styles.userHead}>
                                <NavLink to={`/profile/${u.id}`}>
                                    <div><img className={users_styles.ava}
                                              src={u.photos.small === null ? userPhoto : u.photos.small} alt="userAva"/>
                                    </div>
                                </NavLink>
                                <div className={users_styles.string}>Name: {u.name}</div>
                                <div
                                    className={users_styles.string}>Status: {u.status === null ? 'no status' : u.status}</div>
                            </div>
                            <div className={users_styles.userInfo}>
                                {
                                    props.isAuth ?

                                        u.followed === true
                                            ? <button disabled={props.followingInProgress.some(id => id === u.id)}
                                                      onClick={() => {
                                                          props.unfollow(u)
                                                      }}>Unfollow</button>
                                            : <button disabled={props.followingInProgress.some(id => id === u.id)}
                                                      onClick={() => {
                                                          props.follow(u)
                                                      }}>Follow</button>
                                    : ''
                                }
                            </div>
                        </div>
                    ))
                }
            </div>
        </>
    )
}
export default Users;