import React from "react";
import Header from "./Header";
import { connect } from "react-redux";
import {logoutThunkCreator} from "../../redux/auth-reducer";
import {compose} from "redux";
import {getAuthIsFetching, getEmail, getIsAuth, getLogin, getUserId} from "../../redux/selectors";

class HeaderClassContainer extends React.Component {
    render() {
        return <Header {...this.props} />
    }
}

const mapStateToProps = (state) => {
    return {
        id: getUserId(state),
        login: getLogin(state),
        email: getEmail(state),
        isFetching: getAuthIsFetching(state),
        isAuth: getIsAuth(state),
    };
}

export default compose(connect(mapStateToProps, { logOut: logoutThunkCreator }))(HeaderClassContainer)