import post_mod from '../.././css_modules/Post.module.css';

const Post = (props) => {
    return (
        <div className={post_mod.post_title}>
            <img src='https://cdn-icons-png.flaticon.com/128/3280/3280979.png' className={post_mod.circle} alt='post avatar' />
            <h3>{props.message}</h3>
            <div className={post_mod.btns}>
                <span>Like <span>{props.likes}</span></span><br />
                <span>Dislike <span>{props.dislikes}</span></span>
                <button onClick={() => props.deletePost(props.id)}>Delete</button>
            </div>
        </div>
    );
}

export default Post;
