import React from 'react';
import textarea from "../../../css_modules/Textarea.module.css";

const SetCustomArea = ({input, meta, ...props}) => {
    const errorState = meta.touched && meta.error;
    return (
        <div className={textarea.textWrapper + " " + (errorState ? textarea.error : "")}>
            {props.children}
            <div className={textarea.errorMessageContainer}>{errorState && <span>{meta.error}</span>}</div>
        </div>
    );
};

export const CustomTxtarea = (props) => {
    const {input, meta, ...restProps} = props;
    return <SetCustomArea {...props}>
        <textarea className={textarea.textarea} {...input} {...restProps} />
    </SetCustomArea>
};

export const CustomInput = (props) => {
    const {input, meta, ...restProps} = props;
    return <SetCustomArea {...props}>
        <input className={textarea.input} {...input} {...restProps} />
    </SetCustomArea>
};
