import {callApi} from "../api/api";
import {itemsMap} from "../utils/objectHelpers/objectHelpers";

const FOLLOW = 'socialNetwork/users/FOLLOW';
const UNFOLLOW = 'socialNetwork/users/UNFOLLOW';
const SET_USERS = 'socialNetwork/users/SET-USERS';
const SET_PAGE = 'socialNetwork/users/SET_PAGE';
const SET_USERS_COUNT = 'socialNetwork/users/SET_USERS_COUNT';
const TOGGLE_FETCH = 'socialNetwork/users/TOGGLE_FETCH';
const TOGGLE_FOLLOW = 'socialNetwork/users/TOGGLE_FOLLOW';

export const follow = (userId) => ({type: FOLLOW, userIdx: userId});
export const unfollow = (userId) => ({type: UNFOLLOW, userIdx: userId});
export const setUsers = (users) => ({type: SET_USERS, users});
export const setCurrentPage = (currentPage) => ({type: SET_PAGE, currentPage});
export const setUsersCount = (count) => ({type: SET_USERS_COUNT, count});
export const toggleFetch = (isFetching) => ({type: TOGGLE_FETCH, isFetching});
export const toggleFollowingProgress = (isLoading, userId) => ({type: TOGGLE_FOLLOW, isLoading, userId});


let initialState = {
    users: [],
    totalCount: 0,
    pageSize: 100,
    currentPage: 1,
    isFetching: false,
    followingInProgress: []
};

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: itemsMap(state.users, action.userIdx, "id", {followed: true})
            }
        case UNFOLLOW:
            return {
                ...state,
                users: itemsMap(state.users, action.userIdx, "id", {followed: false})
    }
        case SET_USERS:
            return {
                ...state,
                users: action.users
            }
        case SET_PAGE:
            return {
                ...state,
                currentPage: action.currentPage
            }
        case SET_USERS_COUNT:
            return {
                ...state,
                totalCount: action.count
            }
        case TOGGLE_FETCH:
            return {
                ...state,
                isFetching: action.isFetching
            }
        case TOGGLE_FOLLOW:
            return {
                ...state,
                followingInProgress: action.isLoading
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        default:
            return state;
    }
}

export const getUsersThunkCreator = (currentPage, pageSize) => async (dispatch) => {
    dispatch(toggleFetch(true))
    dispatch(setCurrentPage(currentPage))
    let data = await callApi.getUsers(currentPage, pageSize);
        dispatch(setUsersCount(data.totalCount))
        dispatch(setUsers(data.items));
        dispatch(toggleFetch(false));
};

const followUnfollowFlow = async (dispatch, user, apiCallback, dispatchCallback) => {
    dispatch(toggleFollowingProgress(true, user.id))
    let data = await apiCallback(user);
    if (data.resultCode === 0) {
        dispatch(dispatchCallback(user.id))
        dispatch(toggleFollowingProgress(false, user.id))
    }
}

export const unfollowUser = (u) => (dispatch) => {
    followUnfollowFlow(dispatch, u, callApi.unfollowUser.bind(callApi), unfollow)
}

export const followUser = (u) => (dispatch) => {
    followUnfollowFlow(dispatch, u, callApi.followUser.bind(callApi), follow)
}

export default usersReducer;